import React, { Component } from 'react'

export default class Maintenance extends Component {
    render() {
        return <div>
            {/* <!-- content section --> */}
            <section class="full-screen no-padding xs-no-background" >
                <div class="container">
                    <div class="row">
                        <div class="col-md-10 col-sm-12 col-xs-11 text-center center-col margin-ten no-margin-bottom">
                            {/* <!-- logo --> */}
                            <img class="logo-style-2" src="../logo-light-medium.png" alt="" />
                            {/* <!-- end logo --> */}
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-10 col-sm-12 col-xs-11 center-col text-center margin-five">
                            <h1>Site is Under Maintenance</h1>
                            <p class="coming-soon-title text-uppercase margin-one">Please check back in sometime.</p>
                        </div>
                    </div>
                    <div class="row margin-one">
                        <div class="col-md-4 col-sm-4 col-xs-12 text-center">
                            <i class="icon-paintbrush medium-icon"></i>
                            <p class="text-med text-uppercase font-weight-600 letter-spacing-1 black-text">Why is the Site Down?</p>
                            <p class="width-80 center-col">The site is under maintenance probably because we are working to improve this website drastically.</p>
                        </div>
                        <div class="col-md-4 col-sm-4 col-xs-12 text-center">
                            <i class="icon-clock medium-icon"></i>
                            <p class="text-med text-uppercase font-weight-600 letter-spacing-1 black-text">What is the Downtime?</p>
                            <p class="width-80 center-col">We improve our website. We are usually back within 10-15 minutes but it definitely depends on the issue.</p>
                        </div>
                        <div class="col-md-4 col-sm-4 col-xs-12 text-center">
                            <i class="icon-envelope medium-icon"></i>
                            <p class="text-med text-uppercase font-weight-600 letter-spacing-1 black-text">Do you need Support?</p>
                            <p class="width-80 center-col">If you know someone who needs our support, please contact us. You may simply send us an Email at <a href="mailto:no-reply@domain.com" class="text-decoration-underline">no-reply@domain.com</a></p>
                        </div>
                    </div>
                </div>
            </section>
            {/* <!-- end content section --> */}
        </div>
    }
}