import React from 'react'
import { Switch, Route } from 'react-router-dom'
import Index from './Index'
import Portfolio from './Portfolio'
import Portfolio_Single from './Portfolio_Single'
import About from './About'
import Contact from './Contact'
import Services from './Services'
import Offline from './404'
import Maintenance from './Maintenace'

const Routes = () => (
        <Switch>
            <Route exact path='/' component={Index} />
            <Route path='/portfolio' component={Portfolio} />
            <Route path='/portfolio-single' component={Portfolio_Single} />
            <Route path='/about-us' component={About} />
            <Route path='/contact-us' component={Contact} />
            <Route path='/services' component={Services} />
            <Route path='/404' component={Offline} />
            <Route path='/blog' component={Maintenance} />
        </Switch>
)

export default Routes